import React from 'react'
import {withStyles} from '@material-ui/core/styles'
import {Box, Typography, Card, CardContent,CardMedia,CardHeader} from '@material-ui/core'
import {Slide} from '@material-ui/core'

const style = (theme)=>({
    card:{
        margin:30,
        padding:30
    },
    text:{
        fontFamily: 'Raleway',
        margin:20 
    },
    media:{
        height:50,
        width: 50
    },
    box:{
        margin:10
    }
})


class Skills extends React.Component{

    constructor(props){
        super(props);
    }

    render(){
        const {classes} = this.props; 
        return(
            <Box display="flex" flexDirection="column">
                <Slide direction="left" in ={true}>
                    <Typography variant="h3" className={classes.text}>
                        Skills
                    </Typography>
                </Slide>
                 <Card className = {classes.card} raised>
                     <Box display = "flex" flexDirection="row" alignItems="center" className={classes.box}>
                        <CardMedia
                            image = "https://image.flaticon.com/icons/svg/888/888839.svg"
                            className={classes.media}/>
                        <CardContent>
                            <Typography variant="h5">
                                Android Development With Java
                            </Typography>
                        </CardContent>
                     </Box>
                     <Box display = "flex" flexDirection="row" alignItems="center" className={classes.box}>
                        <CardMedia
                            image = "https://image.flaticon.com/icons/svg/919/919851.svg"
                            className={classes.media}/>
                        <CardContent>
                            <Typography variant="h5">
                                React Front End Development
                            </Typography>
                        </CardContent>
                    </Box>
                    <Box display = "flex" flexDirection="row" alignItems="center" className={classes.box}>
                        <CardMedia
                            image = "https://image.flaticon.com/icons/svg/937/937464.svg"
                            className={classes.media}/>
                        <CardContent>
                            <Typography variant="h5">
                                Back End With Node.js
                            </Typography>
                        </CardContent>
                    </Box>
                     
                </Card> 
                <Card className = {classes.card} raised>
                    <CardMedia
                        image = "https://image.flaticon.com/icons/svg/1822/1822206.svg"
                        component="a"
                        href=""
                        className={classes.media}/>
                    <CardContent>
                        <Typography variant="h5">
                            Data Stuructures and Algorithms
                        </Typography>
                    </CardContent>
                </Card> 
            </Box>
           
            
        )
    }
}


export default withStyles(style)(Skills)