import React from 'react'
import {Box,Card,CardMedia,Typography,Slide,CardContent} from '@material-ui/core'
import {withStyles} from '@material-ui/core/styles'

const style = (theme)=>({
    text:{
        fontFamily: 'Raleway',
        margin:30
    },media:{
        height:50,
        width: 50
    }
})


class Hobbies extends React.Component{
    constructor(props){
        super(props);
    }

    render(){
        const {classes} = this.props;

        return(
            <Box display = "flex" flexDirection="column">
                <Box>
                    <Slide direction="left" in={true}>
                        <Typography className={classes.text} variant="h3">
                            Hobbies
                        </Typography>
                    </Slide>
                    
                </Box>
                <Box>
                    <Slide direction="up" in={true}>
                    <Card raised >
                        <Box display="flex" flexDirection="row" alignItems="center" marginLeft={10}>
                            <CardMedia
                                image="https://www.flaticon.com/premium-icon/icons/svg/2881/2881861.svg"
                                className={classes.media}/>
                            <Typography className={classes.text} variant="h5">
                                Playing Guitar
                            </Typography>
                        </Box>
                        <Box display="flex" flexDirection="row" alignItems="center" marginLeft={10}>
                            <CardMedia
                                image="https://image.flaticon.com/icons/svg/3043/3043892.svg"
                                className={classes.media}/>
                            <Typography className={classes.text} variant="h5">
                                Singing
                            </Typography>
                        </Box>
                        <Box display="flex" flexDirection="row" alignItems="center" marginLeft={10}>
                            <CardMedia
                                image="https://image.flaticon.com/icons/svg/2972/2972291.svg"
                                className={classes.media}/>
                            <Typography className={classes.text} variant="h5">
                                Gaming
                            </Typography>
                        </Box>
                    </Card>
                    </Slide>
                </Box>
            </Box>
        )
    }
}

export default withStyles(style)(Hobbies)
