import React from 'react'
import {withStyles} from '@material-ui/core/styles'
import { Typography ,Grow,Slide, Card, Box} from '@material-ui/core';


const style = (theme)=>({
    root:{
        margin: 40
    },
    text:{
        fontFamily: 'Raleway',
        margin:30
    },
    subtitle:{
        fontFamily: 'Arial',
        margin:theme.spacing(3)
    },card:{
        marginRight:60,
        marginLeft:60
    }
})

class About extends React.Component{

    constructor(props){
        super(props);
    }

    render(){
        const {classes} = this.props;
        return (
            <Box display="flex" flexDirection="column">
                <Slide direction="left" in ={true}>
                    <Typography variant="h3" className={classes.text}>
                        About Me
                    </Typography>
                </Slide>
                <Card raised className={classes.card}>
                    <Slide direction="up" in = {true}>
                        <Typography variant="h5" className={classes.subtitle} >
                            I am an under graduate developer from Varanasi, India. Currently persuing Bachelaors in 
                            Technology with Electronics and Communication Branch from MNNIT Prayagraj, India.

                        </Typography>
                    </Slide>
                </Card>
                
                
            </Box>
            
        )
    }
}

export default withStyles(style)(About)