import React from 'react'
import {Card,Box, CardContent, Typography,Slide, CardMedia} from '@material-ui/core'
import {withStyles} from '@material-ui/core/styles'

const style = (theme)=>({
    text:{
        fontFamily: 'Raleway',
        margin:30
    },
    media:{
        height:50,
        width: 50
    },
    box:{
        height : 70,
        marginLeft: 50,
        marginTop:30
    }
})

class Education extends React.Component{


    constructor(props){
        super(props);
    }

    render(){
        const {classes} = this.props;

        return(
            <Box display="flex" flexDirection="column">
                <Box>
                    <Slide direction="left" in={true}>
                        <Typography className={classes.text} variant="h3">
                            Education
                        </Typography>
                    </Slide>
                    
                </Box>
                <Box>
                    <Slide direction="up" in={true}>
                    <Card raised>
                        <Box display="flex" flexDirection="row" alignItems="center" className={classes.box}>
                            <CardMedia
                                image="https://lh3.googleusercontent.com/proxy/my2TQFUYOuhb4bwLmp0HDqJZn_UcP-Z-JnE807txxuq71IgbW3tHMbNt1NrppzjZiTF9IDdr75H-cYFjAmfhkSg"
                                className={classes.media}/>
                            <Typography className={classes.text} variant="h5">
                                College: Motilal Nehru National Institute of technology
                            </Typography>
                            
                        </Box>
                        <Box display="flex" flexDirection="row" alignItems="center" className={classes.box}>
                            <CardMedia
                                image="https://logosolusa.com/wp-content/uploads/parser/DPS-Logo-1.jpg"
                                className={classes.media}/>
                            <Typography className={classes.text} variant="h5">
                                School: Delhi Public School Varanasi
                            </Typography>

                        </Box>
                    </Card>
                    </Slide>
                </Box>
            </Box>
        )   
    }
}

export default withStyles(style)(Education)