import React from 'react'
import colors from '../../res/Theme'
import {AppBar, Toolbar, IconButton, Typography, Button, Box, Grid} from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import {Email,Phone,GitHub,LinkedIn} from '@material-ui/icons'

import HomeDrawer from './HomeDrawer.js';
import Banner from './Banner';
import TabBar from './TabBar'

const style = theme =>({
    root: {
        position: "static"
    },
    toolbar:{
        display:"flex",
        minHeight: 350,
    },
    title: {
        flexGrow: 1,
        alignSelf: 'flex-end',
        marginRight:"auto",
        paddingBottom :theme.spacing(2),
    },
    drawer:{
        margin:theme.spacing(2)
    },
    main:{
        width:"100%"
    }

});





class Header extends React.Component{

    

    render(){
        const {classes} = this.props;
        
        return(
            <AppBar className= {classes.root}>
                <Toolbar className = {classes.toolbar}>
                    <Box flexWrap="wrap" display="flex"  justifyContent="center">
                        <Banner display="flex"/>
                        <TabBar className={classes.drawer} />
                    </Box>
                    <Box display="flex"  flexDirection="column" alignItems="center">
                        <Box m={2}>
                            <Typography align="center" variant="h4">
                                Connect
                            </Typography>  
                        </Box>
                        
                        <Box display="flex" flexDirection="row">
                            <IconButton color="inherit" >
                                <Email/>
                            </IconButton>
                            <IconButton href = "https://www.linkedin.com/in/om-singh-5092/" color="inherit">
                                <LinkedIn/>
                            </IconButton>
                            <IconButton href="https://github.com/OmSingh5092" color="inherit">
                                <GitHub />   
                            </IconButton>
                        </Box>
                        
                    </Box>
                </Toolbar>
            </AppBar>
            
        )

    }

}
export default withStyles(style)(Header)