import React from 'react'
import {Tabs,Tab} from '@material-ui/core'
import {withStyles,makeStyles} from '@material-ui/core/styles'
import {Link} from 'react-router-dom'
import MenuIcon from '@material-ui/icons/Menu'



const style = (theme)=>({
    list:{
        width:200,
        color:"inherit"
    },
    listItem:{
        padding:theme.spacing(3)
    },
    itemText:{
        fontSize:20
    },
    button:{
        color:"secondary",
        varient:"outlined"
    },
    buttonText:{
        fontSize:100,
    }
})

class TabBar extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            open: false
        }
        //Binding this to toggle Drawer
        this.toggleDrawer = this.toggleDrawer.bind(this);
    }

    toggleDrawer(){
        this.setState({open:!this.state.open});
    }


    render(){
        const {classes} = this.props;
        const links = ["/","/project","/skill","/education","/hobbies"];
        return(
            <Tabs>
                {["About Me","Projects","Skills","Education","Hobbies"].map((text,index)=>(
                   <Tab label={text} component={Link} to={links[index]}></Tab>
                ))}
            </Tabs>


            
        )
    }
}

export default withStyles(style)(TabBar);


