import React from 'react'
import {withStyles} from '@material-ui/core/styles'
import { Box, Typography, Grid,Button} from '@material-ui/core';


const style = (theme)=>({
    root:{
        margin:theme.spacing(3)
    },
    button:{
        height:50,
        margin: 70
    }
})


class Banner extends React.Component{
    constructor(props){
        super(props)

    }


    render(){
        const classes = this.props.classes;
        return(
            <Grid container className = {classes.root}>
                <Grid item xs={12}>
                    <Typography>
                        Hello there! My name is
                    </Typography>
                </Grid>
                <Grid container>
                    <Box display="flex" flexDirection="row" alignItems="center">
                        <Typography variant="h1" align="center" display="block">
                            Om Singh
                        </Typography>
                        <Button color="secondary" variant="outlined" className={classes.button} href="https://docs.google.com/document/d/1awu4D7p9tiU18w3tlolINHnF_qHd2PhIv3PKpNITIA8/edit?usp=sharing">Resume </Button>
                    </Box>
                    
                </Grid>
            </Grid>
        )
    }

}

export default withStyles(style)(Banner);