import React from 'react'
import {Drawer, List,ListItemIcon, ListItemText,Button,ListItem, IconButton, Menu, Typography} from '@material-ui/core'
import {withStyles,makeStyles} from '@material-ui/core/styles'
import {Link} from 'react-router-dom'
import MenuIcon from '@material-ui/icons/Menu'



const style = (theme)=>({
    list:{
        width:200,
        color:"inherit"
    },
    listItem:{
        padding:theme.spacing(3)
    },
    itemText:{
        fontSize:20
    },
    button:{
        color:"secondary",
        varient:"outlined"
    },
    buttonText:{
        fontSize:100,
    }
})

class HomeDrawer extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            open: false
        }
        //Binding this to toggle Drawer
        this.toggleDrawer = this.toggleDrawer.bind(this);
    }

    toggleDrawer(){
        this.setState({open:!this.state.open});
    }


    render(){
        const {classes} = this.props;
        const links = ["/","/project","/skill","/education","/hobbies"];
        return(
            <React.Fragment >
                <Button color="secondary" size="large" classes = {{text:classes.buttonText}}variant="outlined" onClick= {()=> this.toggleDrawer()}>
                    Know Me
                </Button>
                <Drawer anchor="left"  open ={this.state.open} onClose= {()=>this.toggleDrawer()}>
                    <div className={classes.list}>
                        <List onClick = {()=> this.toggleDrawer()}>
                            {["About Me","Projects","Skills","Education","Hobbies"].map((text,index)=>(
                                <ListItem button key={text} component={Link} to ={links[index]} className={classes.listItem}>
                                    <ListItemText primary={text} classes={{primary: classes.itemText}}/>
                                </ListItem>
                            ))}
                        </List>
                    </div>
                    
                </Drawer>
                
            </React.Fragment>


            
        )
    }
}

export default withStyles(style)(HomeDrawer);


