import React from 'react'
import {Card,CardActionArea,CardMedia,CardContent,Typography, CardActions} from '@material-ui/core'
import {Button,Grid,IconButton,Slide} from '@material-ui/core'
import {GitHub,NoteAdd,PhoneAndroid} from '@material-ui/icons'
import {withStyles} from '@material-ui/core/styles'
const style = (theme)=>({
    root:{
        padding: theme.spacing(2),
    },
    title:{
        fontFamily: 'Raleway',
        margin:20
    },
    card:{
        maxWidth:"100%",
        margin: 20,
    },
    media:{
        height:140,
    }
})




class Project extends React.Component{
    constructor(props){
        super(props)
        this.state ={
            data : [{
                title:"Automated Pillbox",
                github: "https://github.com/OmSingh5092/AutomatedPillbox",
                des: "A Pillbox which could remind user about their regular pills.",
                image:"https://static.toiimg.com/photo/msid-74824961/74824961.jpg?219207",
                appetize: ""
            },{
                title:"College Mate",
                github: "https://github.com/OmSingh5092/CollegeMate-1",
                des: "An all in one application for College Students",
                image: "https://miro.medium.com/proxy/1*PAdQnQNhMfaRn6IwnQxPhQ.jpeg",
                appetize:"https://appetize.io/app/21wvht6eue94256j1kutq51yeg?device=nexus5&scale=75&orientation=portrait&osVersion=8.1"
            },{
                title: "Dorm Easy",
                github: "https://github.com/OmSingh5092/DormEasy",
                des : "Hostel Management Application",
                image: "https://4.bp.blogspot.com/-FI8LE3KC1H8/WDvw2uR0rKI/AAAAAAAAAMw/YDI8HnCXIB0kAED48pTMrjJh3sSZksTbgCLcB/s1600/Project%2BManagement%2B-%2Bfreewallpaperimage.xyz%2B.jpg",
                appetize: "https://www.flaticon.com/search?word=appetize",
            }]
        }

    }


    render(){
        const {classes} = this.props;
        return(
            <Grid container className= {classes.root}>
                <Grid item xs = {6}>
                    <Slide direction="left" in={true}>
                        <Typography variant="h3" className={classes.title}>
                            Projects
                        </Typography>
                    </Slide>
                    
                </Grid>
                <Grid container xs = {12}>
                    {this.state.data.map((data)=>(
                        <Grid item xs= {6}>
                            <Card className={classes.card} raised>
                                <CardActionArea href={data.appetize}>
                                <CardMedia
                                    className= {classes.media}
                                    image={data.image}
                                    title="Automated Pillbox"/>
                                    <CardContent>
                                        <Typography gutterBottom variant="h5">
                                            {data.title}
                                        </Typography>
                                        <Typography variant="body2" component = 'p'>
                                            {data.des}
                                        </Typography>
                                    </CardContent>
                                </CardActionArea>
                                <CardActions>
                                    <IconButton href={data.github}>
                                        <GitHub/>
                                    </IconButton>
                                    <IconButton >
                                        <NoteAdd/>
                                    </IconButton>
                                </CardActions>                
                            </Card>
                        </Grid>
                    ))}
                </Grid>
            </Grid>
        )
    }
}

export default withStyles(style)(Project)